import { TypedUseSelectorHook, useDispatch, useSelector } from 'react-redux';
import { store } from '.';
import rootReducer from './rootReducer';
import { ActionApp } from '../features/app/constants';
import { ActionAuth } from '../features/auth/constans';
import { ActionRegister } from '../features/register/contains';
import { ActionForgotPassword } from '../features/forgotPassword/contants';
import { ActionChangePassword } from '../features/changePassword/contants';
import { ActionVerifyOtp } from '../features/verifyOtp/contants';

export type RootState = ReturnType<typeof rootReducer>;
export type AppDispatch = typeof store.dispatch;
export const useAppDispatch = () => useDispatch<AppDispatch>();
export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;

export type ResponseApi = {
  status: number;
  code: number;
  data: any;
  message?: string;
};

export const ActionContainer = {
  ...ActionApp,
  ...ActionAuth,
  ...ActionChangePassword,
  ...ActionForgotPassword,
  ...ActionRegister,
  ...ActionVerifyOtp,
} as const;
